import React from 'react';
import { graphql, StaticQuery } from 'gatsby';
// import Img from "gatsby-image";

import Layout from '../components/layout';
import SEO from '../components/seo';

import '../utils/normalize.css';
import '../utils/css/screen.css';
import '../utils/css/global.scss';

const AboutPage = ({ data, location }) => {
    const siteTitle = data.site.siteMetadata.title;
    const description = data.site.siteMetadata.description;
    const facebook = data.site.siteMetadata.social.facebook;
    return (
        <Layout location={location} title={siteTitle} description={description} facebook={facebook}>
            <SEO title="About" />

            <article className="post-content page-template no-image">
                <div className="post-content-body">
                    <h2 id="clean-minimal-and-deeply-customisable-london-is-a-theme-made-for-people-who-appreciate-simple-lines-"></h2>
                    {/* <figure className="kg-card kg-image-card kg-width-full">
            <Img
              fluid={data.benchAccounting.childImageSharp.fluid}
              className="kg-image"
            />
            <figcaption>Large imagery is at the heart of this theme</figcaption>
          </figure> */}
                    <p>嗨你好，我是Ian，目前在軟體服務公司擔任網頁前端工程師。</p>
                    <p>Hi,i am Ian. I’m a FrontEnd Developer, working for SAAS and CMS company.</p>
                    <p>
                        我曾經負責過行銷，所以我也懂點行銷，Google Adword、Google Analytics、SEO。
                    </p>
                    <p>
                        某一天家裡招牌店要做網站，所以開始接觸
                        HTML、CSS、JavaScript，因緣際會之下，就轉到前端了。
                    </p>
                    <p>更多想說的，就在文章裡面慢慢說吧…🤗</p>
                </div>
            </article>
        </Layout>
    );
};

const indexQuery = graphql`
    query {
        site {
            siteMetadata {
                title
                description
                social {
                    facebook
                }
            }
        }
        benchAccounting: file(relativePath: { eq: "bench-accounting-49909-unsplash.jpg" }) {
            childImageSharp {
                fluid(maxWidth: 1360) {
                    ...GatsbyImageSharpFluid
                }
            }
        }
    }
`;

export default props => (
    <StaticQuery
        query={indexQuery}
        render={data => <AboutPage location={props.location} data={data} {...props} />}
    />
);
